<template>
	<div class="import-mnemonics">
		<div style="margin: 40px;text-align: center;">
			<p style="font-size: 16px;margin: 30px 0;">验证你的钱包助记词</p>
			<p style="height: 20px;font-size: 12px;color: #a5a5a5;text-align: center;">请输入备份的钱包助记词(12个英文单词)，按空格分离</p>
			<div style="background: white;">
				<mu-text-field v-model="memorizingWords" style="padding: 10px;border: 1px solid #e5e5e5;" multi-line :solo="true" :rows="6" full-width></mu-text-field><br/>
			</div>
			<p style="color: #b5b5b5;margin-top: 20px;text-align: left;">设置交易密码</p>
			<deal-password v-model="password"></deal-password>
			<p style="color: #b5b5b5;margin-top: 20px;text-align: left">确认交易密码</p>
			<deal-password v-model="dealPasswordAgain"></deal-password>
			<mu-button class="creatBtn" @click="importWallet" style="width: 100%;margin-top: 40px;height: 44px;" color="#295DF1">导入钱包</mu-button>
		</div>

		<!--弹窗-->
		<div v-if="importFail" class="white_content">
			<div style="height: 100px;text-align: center;line-height: 100px;">
				导入失败,是否返回创建钱包
			</div>
			<div class="btn">
				<div class="btn-cancel" @click="importAgain()">重新导入</div>
				<div class="btn-sure" @click="goCreatWallet()">创建钱包</div>
			</div>
		</div>
		<!--覆盖层-->
		<div v-if="importFail" class="black_overlay"></div>
	</div>
</template>

<script>
	import DealPassword from '@/components/dealPassword'
	export default {
		name: 'ImportMnemonics',
		components:{
	  	'deal-password':DealPassword
	  },
		data() {
			return {
				memorizingWords: 'half twist zero bronze brush rule dust later easy buzz parade nation',
				money: '',
				importFail: false,
				password:'',
				dealPasswordAgain:''
			}
		},
		methods: {
			creatWallet() {
				if(this.password==''){
					this.$layer.open({
						content: '请输入交易密码'
					    ,skin: 'msg'
					    ,time: 2 //2秒后自动关闭
					})
					return
				}else if(this.dealPasswordAgain==''){
					this.$layer.open({
						content: '请确认交易密码'
					    ,skin: 'msg'
					    ,time: 2 //2秒后自动关闭
					})
					return
				}else if(this.dealPasswordAgain!=this.password){
					this.$layer.open({
						content: '两次密码输入不一致'
					    ,skin: 'msg'
					    ,time: 2 //2秒后自动关闭
					})
					return
				}
				let _this = this;
				let ajaxLoading = _this.$layer.open({
					type: 2,
					content: '导入中...'
				})
				let walletInfo = {};
				let localStorageWalletArr = [];
//				let secretSeed = lightwallet.keystore.generateRandomSeed(); // 生成助记词
//				console.log("助记词:", secretSeed); // 助记词
				let address = ""; // 地址
				let privateKey = ""; // 私钥
				let hdPathString = "m/44'/60'/0'/0";
				if(_this.memorizingWords.split(' ').length!=12){
					_this.$layer.open({
						content: '助记词格式输入错误或不是12个单词'
					    ,skin: 'msg'
					    ,time: 2 //2秒后自动关闭
					})
					return
				}
				lightwallet.keystore.createVault({
					password: _this.dealPasswordAgain,
					seedPhrase: _this.memorizingWords,
					hdPathString: hdPathString
				}, function(err, ks) {
					ks.keyFromPassword(_this.dealPasswordAgain, function(err, pwDerivedKey) {
						_this.$layer.close(ajaxLoading);
						if(err) {
							_this.importFail = true;
						} else {
							ks.generateNewAddress(pwDerivedKey, 1);
							address = ks.getAddresses()[0];
							privateKey = ks.exportPrivateKey(address, pwDerivedKey);
							console.log("地址:", address); // 地址
							console.log("私钥:", privateKey); // 私钥
							//          console.log(web3.eth.getBalance(address));//余额
							//          	web3 = new Web3(new Web3.providers.HttpProvider("http://192.168.0.136:8080"));
							if(localStorage.getItem('walletArr')){
								localStorageWalletArr = JSON.parse(localStorage.getItem('walletArr'));
								if(localStorageWalletArr.length>0){
									for(let i in localStorageWalletArr){
										if(localStorageWalletArr[i].secretSeed == _this.memorizingWords){
											_this.$layer.open({
												content: '钱包已存在!!!'
											    ,skin: 'msg'
											    ,time: 2 //2秒后自动关闭
											})
											return
										}
									}
								}
							}
							if(localStorage.getItem('walletArr') == '' || localStorage.getItem('walletArr') == 'undefined' || localStorage.getItem('walletArr') == null) {
								walletInfo = {
									walletName: 'HD-新钱包',
									secretSeed: _this.memorizingWords,
									address: address,
									privateKey: privateKey,
									pwd:_this.dealPasswordAgain,
									isUse: true,
									//							balance:web3.eth.getBalance(address)
								}
								localStorage.setItem('chooseWallet',JSON.stringify(walletInfo));
							} else {
								walletInfo = {
									walletName: 'HD-新钱包',
									secretSeed: _this.memorizingWords,
									address: address,
									privateKey: privateKey,
									pwd:_this.dealPasswordAgain,
									isUse: false,
									//							balance:web3.eth.getBalance(address)
								}
							}
							localStorageWalletArr.push(walletInfo)
							localStorage.setItem('walletArr', JSON.stringify(localStorageWalletArr))
							_this.$router.push('/importSuccess')
						}
					});
				});
			},
			//导入钱包
			importWallet() {
				this.creatWallet();
			},
			//重新导入
			importAgain() {
				this.importFail = false;
			},
			//创建钱包
			goCreatWallet() {
				this.$router.push('/creatWallet')
			}

		},
		computed: {

		},
		watch:{
			dealPasswordAgain(newV,oldV){
		  		if(newV.length == 6){
			  		if(newV!=this.password){
			  			this.$layer.open({
								content: '两次密码输入不一致'
							    ,skin: 'msg'
							    ,time: 2 //2秒后自动关闭
							})
			  			this.dealPasswordAgain=''
			  		}
		  		}
		  	}
		}
	}
</script>

<style scoped>
	.import-mnemonics {}
	/**
弹窗样式
**/
	
	.import-mnemonics .white_content {
		position: fixed;
		top: 50%;
		left: 50%;
		width: 90%;
		min-height: 150px;
		border: 1px solid #D6E9F1;
		border-radius: 8px;
		background-color: white;
		z-index: 9999;
		transform: translate(-50%, -50%);
	}
	
	.import-mnemonics .btn {
		height: 50px;
		line-height: 50px;
		position: absolute;
		bottom: 0;
		width: 100%;
		border-top: 1px solid #f5f5f5;
	}
	
	.import-mnemonics .btn .btn-sure {
		color: #295DF1;
		float: right;
		font-size: 16px;
		width: 50%;
		background-color: white;
		border-bottom-right-radius: 8px;
		text-align: center;
	}
	
	.import-mnemonics .btn .btn-cancel {
		color: #295DF1;
		float: left;
		font-size: 16px;
		width: 50%;
		background-color: white;
		border-bottom-left-radius: 8px;
		border-right: 1px solid #f5f5f5;
		text-align: center;
	}
	/**
 * 覆盖层样式
 */
	
	.import-mnemonics .black_overlay {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, .4);
		z-index: 1002;
		opacity: .80;
	}
</style>


